/* Space out content a bit */
body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

/* Everything but the jumbotron gets side spacing for mobile first views */
.header,
.marketing,
.footer {
  padding-left: 15px;
  padding-right: 15px;
}

.navbar {
  margin-bottom: 0;
  background-color: white;
  a {
    text-decoration: none;
  }
  .navbar-brand {
    .navbar-item {
      color: rgba(250, 10, 0, 0.9);
    }
    &:hover {
      .navbar-item {
        color: rgba(0, 0, 128, 0.9);
      }
    }
  }
  .navbar-item a {
    color: rgba(0, 0, 128, 0.9);
    &:hover {
      color: rgba(250, 10, 0, 0.9);
    }
  }
  .navbar-menu.is-active {
    position: absolute;
    right: 0;
    top: 4rem;
    width: 200px;
  }
}
/* Custom page footer */
.footer {
  padding-top: 16px;
  padding-bottom: 16px;
  color: #777;
  text-align: center;
  .fa-facebook {
    &:hover {
      color: #3b5998;
    }
  }
  .fa-instagram {
    &:hover {
      color: #3f729b;
    }
  }
}

.container-fluid {
  padding: 0;
}

header {
  background: linear-gradient(rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.4)), url(/assets/images/flag.jpg);
  background-size: cover;
  position: relative;
  overflow: hidden;
  padding-bottom: 16px;

  .main-header {
    text-align: center;
    margin: 0 auto;
    padding: 5% 2% 2%;

    h1 {
      font-size: 3rem;
      color: rgba(0, 10, 100, 0.8);
    }
  }

  .main-copy {
    width: 80%;
    padding: 8px 16px;
    margin: 2% auto 0;
    background: rgba(0, 10, 100, 0.9);
    color: white;
    h2,
    h4 {
      text-align: center;
      margin: 0;
      margin-bottom: 1%;
    }
    h2 {
      font-size: 3em;
    }
    h4 {
      font-size: 1.8em;
    }
    p {
      font-size: 1.5em;
      a {
        color: rgba(250, 10, 0, 0.9);
      }
    }
  }
}

.map {
  width: 80%;
  margin: 0 auto;
}

.main-content {
  width: 80%;
  margin: 0 auto;
  padding: 5% 2%;
  min-height: 93vh;
  h2 {
    text-align: center;
    margin-bottom: 2%;
  }
}
.main-img {
  text-align: center;
  padding-top: 3%;
  img {
    width: 80%;
    margin: 0 auto;
  }
}

footer {
  position: relative;
  background: white;
  .copyright {
    clear: both;
  }
  .social {
    position: absolute;
    right: 2%;
    top: 0;
    a {
      display: inline-block;
      text-align: center;
      color: #777;
      font-size: 1.5em;
      margin-right: 8px;
    }
  }
}

/***** About Page ******/
.meet-team img {
  width: 180px;
  height: 250px;
  margin: 0 auto;
}

.img-label {
  text-align: center;
  font-weight: bold;
  a {
    color: rgba(0, 10, 100, 0.8);
  }
}

.about-flags {
  text-align: center;
  color: rgba(0, 10, 100, 0.8);
}

.about-copy {
  width: 100%;
  margin: 0 auto;
  margin-top: 4%;
  position: relative;
  p {
    width: 80%;
    margin: 0 auto 2%;
    font-size: 1.2em;
    a {
      color: rgba(0, 10, 100, 0.8);
    }
  }
}

.edit-container {
  height: 100vh;
  padding-top: 5%;
  .content-container {
    padding-left: 4%;
    label {
      margin-top: 2%;
      display: block;
    }
    input,
    textarea {
      margin-bottom: 2%;
      width: 30%;
    }
    textarea {
      height: 15vh;
    }
    .paragraph-container {
      textarea {
        width: 80%;
      }
    }
  }
}

button,
button.btn-info,
button.btn-success {
  outline: none;
  &:focus {
    outline: none;
  }
}

/* Responsive: Portrait tablets and up */
@media screen and (min-width: 768px) {
  /* Remove the padding we set earlier */
  .header,
  .marketing,
  .footer {
    padding-left: 0;
    padding-right: 0;
  }

  /* Space out the masthead */
  .header {
    margin-bottom: 30px;
  }

  /* Remove the bottom border on the jumbotron for visual effect */
  .jumbotron {
    border-bottom: 0;
  }
  header {
    .main-copy {
      width: 60%;
    }
  }
}

@media screen and (min-width: 992px) {
  .about-row {
    padding: 0 15%;
  }
}

@media screen and (min-width: 1200px) and (max-height: 750px) {
  header {
    overflow: scroll;
    .main-header {
      h1 {
        font-size: 4em;
      }
    }
    .main-copy {
      margin: 0 auto;
    }
  }
}
